var React=require("react");
var highlight=require("./highlight.js");

var ResultItem = React.createClass({displayName: "ResultItem",
  getInitialState:function() {
    return {};
  },
  renderText: function(item){
    if(!item) return React.createElement("div", {className: "inline"})
    return (
      React.createElement("div", {className: "inline"}, 
        React.createElement("span", {className: "hl"+item[0]}, item[1])
      )
    )
  },
  render: function() {
    var hlTextArr=highlight(this.props.text,this.props.realHits);
    if(hlTextArr) var hlText=hlTextArr.map(this.renderText);
    return React.createElement("div", null, 
      this.props.segname, 
      hlText
    );
  }
});
module.exports=ResultItem;