var React=require("react");
var kse=require("ksana-search");
var ResultItem=require("./resultItem.jsx");

var resultList = React.createClass({displayName: "resultList",
  getInitialState:function() {
    return {};
  },
  renderResItem: function(item){
    return React.createElement("div", null, 
      React.createElement(ResultItem, {text: item.text, realHits: item.realHits, segname: item.segname}), 
      React.createElement("br", null)
    )
  },
  render: function() {
    var items=this.props.excerpt.map(this.renderResItem);

    return React.createElement("div", null, items
    );
  }
});
module.exports=resultList;